@font-face {
  font-family: 'Roboto';
  src: url(./fonts/Roboto-Regular.ttf) format('truetype');
}

.page {
  font-family: 'Roboto', Arial, sans-serif;
  font-style: normal;
  font-weight: normal;
  background: black;
  color: #FFFFFF;
  max-width: 660px;
  height: 100%;
  margin: 0 auto;
  width: 100%;
  position: relative;
}

.header__title {
  width: 100%;
  text-align: center;
  color: #ef7332;
  font-weight: 700;
  font-size: 55px;
  margin: 10px auto 20px auto;
}

.content {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.audio-cards {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.audio-cards__card:nth-child(1) {
  width: 200px;
  height: 200px;
  background-image: url(images/summer-bg.jpg);
  background-size: 200px 200px;
  background-position: cover;
  background-repeat: no-repeat;
}

.audio-cards__card:nth-child(2) {
  width: 200px;
  height: 200px;
  background-image: url(images/rainy-bg.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.audio-cards__card:nth-child(3) {
  width: 200px;
  height: 200px;
  background-image: url(images/winter-bg.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}